.player{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
   

}
.time-control {
    width: 50%;
    display: flex;
    input{
        width: 100%;
        padding: 0rem 0rem;
        -webkit-appearance: none;
        background: transparent;
        cursor: pointer;
    }
    p{
        padding: 1rem;
    }
}
.play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 30%;
    play {
        display: none;
    }
    svg {
        cursor: pointer;
    }
}

input[type="range"]:focus{  
    outline: none;
}
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
    background: white;
    border-radius: 50%;
    
}
.track{
    background: lightblue;
    width: 100%;
    height: 1rem;
    margin-top: 1.15rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}
.animate-track{
   background: rgb(204, 204, 204); 
   width: 100%;
   height: 100%;
   position: absolute;
   top: 0;
   left: 0;
   transform: translateX(100%);
   pointer-events: none;
}
@media screen and (max-width: 768px) {
    .time-control {
        width: 90%;
    }
    .play-control {
        width: 60%;
    }
}